import { NextResponse } from 'next/server';
import { PrismaClient } from '@prisma/client';
const prisma = new PrismaClient();            

import { NextRequest, NextResponse } from 'next/server'; // Add NextRequest

export async function GET(request: NextRequest) { // Change type to NextRequest
  try {
    const showDeleted = request.nextUrl.searchParams.get('deleted') === 'true';
    const recipes = await prisma.recipe.findMany({
      where: { isDeleted: showDeleted },
      include: { /* ... include logic is the same ... */ },
      orderBy: { name: 'asc' },
    });
    return NextResponse.json(recipes);
  } catch (error) { /* ... */ }
}